<template>
  <div class="casino container">
    <h1>EtherGalaxy</h1>
    ID: <input v-model="amount" placeholder="1">
	Other: <input v-model="other" placeholder="1">
    <ul>
      <li v-on:click="newStar">0</li>
	  <li v-on:click="newPlanet">0</li>
	  <li v-on:click="newStation">0</li>
	  <li v-on:click="newShip">0</li>
	  <li v-on:click="starProduce">0</li>
	  <li v-on:click="planetProduce">0</li>
	  <li v-on:click="moveShip">0</li> 
    </ul>
  </div>
</template>

<script>
export default {
  name: 'casino',
  data () {
    return {
      amount: null,
      other: null
    }
  },
  methods: {
    newStar (event) {
      this.$store.state.contractInstance().newStar(amount, {
        gas: 200000,
        value: 0,
        from: this.$store.state.web3.coinbase
      })
    },
	newPlanet (event) {
      this.$store.state.contractInstance().newPlanet(amount, {
        gas: 200000,
        value: 0,
        from: this.$store.state.web3.coinbase
      })
    },
	newStation (event) {
      this.$store.state.contractInstance().newStation(amount, {
        gas: 200000,
        value: 0,
        from: this.$store.state.web3.coinbase
      })
    },
	newShip (event) {
      this.$store.state.contractInstance().newShip(amount, other, other, other, other, {
        gas: 300000,
        value: 0,
        from: this.$store.state.web3.coinbase
      })
    },
	starProduce (event) {
		this.$store.state.contractInstance().assetProduce(true, amount, {
			gas: 200000,
			value: 0,
			from: this.$store.state.web3.coinbase
      })
    },
	planetProduce (event) {
		this.$store.state.contractInstance().assetProduce(false, amount, {
			gas: 200000,
			value: 0,
			from: this.$store.state.web3.coinbase
      })
    },
	moveShip (event) {
		this.$store.state.contractInstance().shipMove(amount, other, {
			gas: 200000,
			value: 0,
			from: this.$store.state.web3.coinbase
      })
    }
  },
  mounted () {
    console.log('dispatching getContractInstance')
    this.$store.dispatch('getContractInstance')
  }
}
</script>

<style scoped>
.casino {
     margin-top: 50px;
     text-align:center;
}
#loader {
  width:150px;
}
ul {
    margin: 25px;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-column-gap:25px;
    grid-row-gap:25px;
}
li{
    padding: 20px;
    margin-right: 5px;
    border-radius: 50%;
    cursor: pointer;
    background-color:#fff;
    border: -2px solid #bf0d9b;
    color: #bf0d9b;
    box-shadow:3px 5px #bf0d9b;
}
li:hover{
    background-color:#bf0d9b;
    color:white;
    box-shadow:0px 0px #bf0d9b;
}
li:active{
    opacity: 0.7;
}
*{
   color: #444444;
}
#has-won {
  color: green;
}
#has-lost {
  color:red;
}
</style>



